<template>
  <div class="g-funding scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
    <div class="m-swiper">
      <cube-slide :showDots="showDots" :data="items"/>
    </div>
    <div class="m-border"></div>
    <div class="m-title">
      <i></i>
      <span>
        热门推荐
      </span>
      <i></i>
    </div>
    <div class="m-list">
      <router-link v-for="(v,i) in  list" :to="{name:'fundetails',query:{id:v.id}}" class="s-box">
      <div class="img">
        <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.carousels[0]" alt="">
      </div>
      <div class="content">
        <span class="beyond">{{v.proName}}</span>
        <div class="range">
          <div>
            <span :style="{'width':v.percentage+'%'}"></span>
          </div>
          <i>{{v.percentage}}%</i>
        </div>
        <div class="copies">
          <div class="money">
            <span>已筹资</span>
            <b>{{v.raiseIntegral}}积分</b>
          </div>
          <div class="number">
            <span>支持数</span>
            <b>{{v.raiseNumber}}</b>
          </div>
        </div>
        <div class="target">
          <div>目标金额：{{v.fixIntegral}}积分</div>
          <span>积分：{{v.price}}</span>
        </div>
      </div>
      </router-link>
    </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: "funding",
    data() {
      return {
        showDots: false,
        items: [],
        list: [],/*商品数据*/
        options: {
          /*滚动配置*/
          scrollbar: {fade: true},
          pullUpLoad: {txt: {noMore: '没有更多数据了'}},
          pullDownRefresh: {stopTime: 0, txt: ' '}
        },
        bit:{
          page:1,
          sortId:2,
        }
      }
    },
    created(){
      this.swiper();//轮播图
      this.goodList();//列表
    },
    methods:{
      swiper(){
        this.$post(this.$bee+'/api/index/listAdvert',{place:3}).then((data)=>{
          if(data.code==200){
            data.data.adverts.forEach((i)=>{
              this.items.push({url:(i.url?i.url:'javascript:void(0)'),image:'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+i.pic})
            })
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      goodList(){
        this.$post(this.$bee+'/api/product/listProduct',this.bit).then((data)=> {
          if (data.code == 200&&data.data.pros.length!=0) {
            data.data.pros.forEach((i) => {
              this.list.push(i)
            })
            this.bit.page++;
          } else {
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      onPullingDown() {/*下拉刷新*/
        this.bit.page=1;
        this.list=[];
        this.goodList();
      },
      onPullingUp() {/*上拉加载*/
        this.goodList();
      }
    }
  }
</script>

<style scoped lang="stylus">
  .g-funding
    .m-swiper
      width 100%
      & >>> .cube-slide
        width 100%
        img
          width 100%
          height 100%
    .m-border
      height 5px
      width 100%
      background-color #ebebeb
    .m-title
      position relative
      margin-bottom 12.5px
      span
        color #1a1a1a
        font-size 16px
        height 30px
        line-height 30px
      i
        position absolute
        top calc(50% - 0.5px)
        width 24px
        height 1px
        background #6d6d6d
        &:nth-child(1)
          left 33%
        &:nth-child(3)
          right 33%
    .m-list
      padding 0 12.5px
      .s-box
        display flex
        flex-wrap wrap
        border-bottom 1px solid #f0f0f0
        box-sizing border-box
        padding 12.5px 0
        &:last-child
          border 0
        .img
          width 120px
          height 100px
          background-color #72c5ff
          border-radius 5px
          img
            width 100%
            height 100%
        .content
          width calc(100% - 130px)
          padding-left 10px
          &>span
            color #4b4b4b
            font-size 13.5px
            display block
            text-align left
            margin-bottom 5px
            padding 5px 0
          .range
            position relative
            height 15px
            i
              font-size 11px
              color #55acef
              font-style normal
              position absolute
              right 0
              top 1px
            div
              width 180px
              height 8px
              background #dfdfdd
              border-radius 2.5px
              position relative
              overflow hidden
              top 3px
              span
                position absolute
                bottom 0
                top 0
                left 0
                border-radius 2.5px
                background #55acef
          .copies
            font-size 12px
            margin-top 20px
            text-align left
            &>div
              display inline-block
            .money
              position relative
              &:after
                position absolute
                content ''
                right -15px
                top 1px
                width 1px
                height 10px
                background #dfdfdd
              span
                color #55acef
              b
                color #1a1a1a
                margin-left 5px
            .number
              margin-left 32.5px
              span
                color #a0a0a0
              b
                color #1a1a1a
                margin-left 5px
          .target
            font-size 0
            margin-top 14px
            text-align left
            div,span
              display inline-block
            div
              color #6d6d6d
              font-size 12px
            span
              font-size 13.5px
              color #c38b30
              float right
</style>
